<template>
  <div class="pageone">
    <div><el-button @click="click1" size="small" type="primary">输出数据</el-button></div>
    <div><el-input v-model="input" size="small" placeholder="请输入内容"></el-input></div>
    <div><el-button @click="click2" size="small" type="primary">修改数据</el-button></div>
    <div><el-input v-model="input2" size="small" placeholder="请输入内容"></el-input></div>
    <div><el-button @click="click3" size="small" type="primary">调用action</el-button></div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data() {
    return {
      input: '',
      input2: '',
    }
  },
  name: 'home',
  created() {
  },
  computed: {
    ...mapState({
      treeData: (state) => state.common.treeData,
    }),
  },
  methods: {
    click1() {
      console.log(this.treeData)
    },
    click2() {
      // 修改仓库数据
      this.$store.commit('SET_TREE_DATA', this.input)
    },
    click3() {
      // 通过请求修改仓库数据
      this.$store.dispatch('setTree', this.input2)
    },
  },
  components: {},
}
</script>
<style lang="scss" scoped>
.pageone{
  width: 100%;
  height: 100%;
  &>div{
    padding: 10px 0;
  }
}
</style>
